import React from "react";
import { Link } from "react-router-dom";
import "./Header.css";

const Header = ({ toggleNavbar, isNavVisible, setShowLoginModal, isLoggedIn, setIsLoggedIn }) => {

  const user = JSON.parse(localStorage.getItem('user'));

  const handleLogout = () => {
    localStorage.clear();
    setIsLoggedIn(false);
  };

  //      
  const getUserRole = (status) => {
    switch (status) {
      case 'a':
        return '';
      case 'm':
        return '';
      case 'u':
        return '';
      default:
        return '';
    }
  };

  return (
    <header className="header">
      <h1 className="header-title">
        <Link to="/" className="Header-button">HANCHEOL ERP</Link>
      </h1>

      {/*      */}
      <div className="login-section">
        {isLoggedIn ? (
          <div className="loginState">
            <p>, {user.userName}! ({getUserRole(user.status)})</p>
            <button className="header-button" onClick={handleLogout}></button>
          </div>
        ) : (
          <div className="no-login">
            <button className="header-button" onClick={() => setShowLoginModal(true)}></button>
            <Link to="/Signup" style={{ textDecoration: 'none' }}>
              <button className="header-button">
                
              </button>
            </Link>
          </div>
        )}
      </div>

      <button className="navbar-toggle header-button" onClick={toggleNavbar}>
        {isNavVisible ? "" : ""}
      </button>
    </header>
  );
};

export default Header;
