import React, { useState } from "react";
import "./Purchase.css"; //    CSS
import * as PortOne from "@portone/browser-sdk/v2";

const Purchase = ({ item, closeModal }) => {
  //   
  const [quantity, setQuantity] = useState(1);
  const [errorMessage, setErrorMessage] = useState(""); //   

  if (!item) return null; // item    

  const handleQuantityChange = (e) => {
    const value = e.target.value;

    //   
    if (value === "") {
      setQuantity("");
      return;
    }

    //   
    const newQuantity = Math.min(Math.max(1, Number(value)), item.itemQuantity);
    setQuantity(newQuantity);
  };

  //     
  const handleQuantityBlur = () => {
    if (quantity === "" || quantity < 1) {
      setQuantity(1); // 
    }
  };

  const handlePayment = async () => {
    try {
      //    3   
      const validHours = 3; // : 3   
      const dueDate = new Date(); //    
  
      // validHours 
      const expirationDate = new Date();
      expirationDate.setHours(expirationDate.getHours() + validHours);
      const validHoursString = expirationDate.toISOString(); // ISO  
  
      // , dueDate 
      // const dueDateString = "20241231"; // YYYYMMDD 
      // const dueDateString = "2024-12-31"; // YYYY-MM-DD 
      // const dueDateString = "2024-12-31 15:00:00"; // YYYY-MM-DD HH:mm:ss 
  
      const response = await PortOne.requestPayment({
        storeId: "store-3f9ebc91-a91b-4783-8f05-da9c39896c3a", //  storeId 
        paymentId: `payment-${crypto.randomUUID()}`,
        orderName: item.itemName, // 
        totalAmount: item.price * quantity, //  
        currency: "CURRENCY_KRW",
        channelKey: "channel-key-86c5c97a-6f3c-45db-8b06-73b316d1788f", //  
        payMethod: "VIRTUAL_ACCOUNT",
  
        //    
        virtualAccount: {
          // validHours 
          accountExpiry: {
            validHours: validHours, //  validHoursString 
            // dueDate: dueDateString //  dueDate 
          },
          bankCode: "KOOKMIN_BANK", // :  
          accountHolderName: "", //   
          accountHolderBirthday: "980518", //    (YYYYMMDD )
        },
      });
  
      //    
      if (response.code !== undefined) {
        alert(response.message); //   
        return;
      }
  
      //     
      const userNum = localStorage.getItem("userNum");
      const userId = localStorage.getItem("userId");
  
      const notified = await fetch("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/payment/complete", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": "PortOne utFxMAsGVKNYWxXKECqU3WTkGrwnLnSjLuHbrhtds3MxieauTc9YSzEuxaycVPHAxl2DHGJnTiEkKdBS", //  
        },
        body: JSON.stringify({
          paymentId: response.paymentId,
          itemNum: item.itemNum, //  
          totalPrice: item.price * quantity, //  
          totalCostPrice: item.costPrice * quantity, //  
          salesQuantity: quantity, // 
          payType: "Easy_Pay",
          userNum,
          userId,
        }),
      });
  
      const result = await notified.json();
      if (result.success) {
        alert(" .");
        closeModal();
      } else {
        alert("  .");
      }
    } catch (error) {
      console.error(" :", error);
      alert("    .");
    }
  };
  
  

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2> </h2>
        <div className="align-left">
          <p><strong> : </strong> {item.itemName}</p>
          <p><strong> : </strong> {item.price.toLocaleString()} </p>
          <div>
            <strong> : </strong>
            <input
              type="number"
              value={quantity}
              onChange={handleQuantityChange}
              onBlur={handleQuantityBlur} //    
              className="quantity-input"
            /> 
            <span className="perchase-quantity"> ({item.itemQuantity}  )</span>
          </div>
          <span className="red-text">*   .</span>
        </div>
        {errorMessage && <p className="error-message">{errorMessage}</p>} {/*    */}
        <hr />
        <div className="align-left">
          <strong>  : {(item.price * quantity).toLocaleString()}</strong>
        </div>
        <div>
          <button onClick={handlePayment} className="perchase-button"></button>
          <button onClick={closeModal} className="close perchase-button"></button>
        </div>
      </div>
    </div>
  );
};

export default Purchase;
