import React, { useState, useEffect } from "react";
import axios from "axios";
import Pagination from "../../layout/Pagination.js";
import './InOutInfo.css';  // CSS  

const InOutInfo = () => {
  const [itemHistory, setItemHistory] = useState([]);
  const [filters, setFilters] = useState({
    pageNum: 1,
    order: 0,  //   
  });
  const [pageInfo, setPageInfo] = useState({});

  useEffect(() => {
    const fetchItemHistory = async () => {
      try {
        // filters query    
        const query = new URLSearchParams(filters).toString();  
        const response = await axios.get(`https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/item/itemHistory?${query}`);
        const { items, count } = response.data;

        setItemHistory(items);  //   
        setPageInfo({ count: count });
      } catch (error) {
        console.error("Error fetching item history:", error);
      }
    };

    fetchItemHistory();
  }, [filters]);  // filters   

  const handleOrderChange = (order) => {
    setFilters((prev) => {
      return { ...prev, order, pageNum: 1 };  //   1 
    });
  };

  return (
    <div>
      <table className="table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th>/</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {pageInfo.count > 0 ? (
            itemHistory.map((item, index) => (
              <tr key={index}>
                <td>{item.historyNum}</td>
                <td>{item.itemNum}</td>
                <td className={item.transactionType === '0' ? "transaction-out" : "transaction-in"}>
                  {item.transactionType === '0' ? "" : ""}
                </td>
                <td>{item.transactionQuantity}</td>
                <td>{new Date(item.transactionDate).toLocaleDateString()}</td>
                <td>{item.transactionNotes}</td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="5" className="empty-message"> .</td>
            </tr>
          )}
        </tbody>
      </table>

      <div className="order-radio">
        <label>
          <input
            type="radio"
            name="order"
            value="1"
            onChange={() => handleOrderChange(1)}
            checked={filters.order === 1}
          />
           
        </label>
        <label>
          <input
            type="radio"
            name="order"
            value="2"
            onChange={() => handleOrderChange(2)}
            checked={filters.order === 2}
          />
           
        </label>
        <label>
          <input
            type="radio"
            name="order"
            value="3"
            onChange={() => handleOrderChange(3)}
            checked={filters.order === 3}
          />
           
        </label>
        <label>
          <input
            type="radio"
            name="order"
            value="4"
            onChange={() => handleOrderChange(4)}
            checked={filters.order === 4}
          />
           
        </label>
      </div>

      {/* Pagination   */}
      {pageInfo.count > 0 && (
        <Pagination
          currentPage={filters.pageNum}
          count={pageInfo.count}
          setFilters={setFilters}
        />
      )}
    </div>
  );
};

export default InOutInfo;
