import React, { useState, useEffect } from "react";
import "./ProjectInfo.css";
import TaskLogTable from "./TaskLogTable";

const ProjectInfo = () => {
  const [progress, setProgress] = useState([]);
  const [editIndex, setEditIndex] = useState(null);
  const [editedData, setEditedData] = useState({});
  const [newProgress, setNewProgress] = useState(null); //   
  const user = JSON.parse(localStorage.getItem('user'));

  //    
  const fetchProgress = async () => {
    try {
      const response = await fetch(
        "https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/ProjectProgress/all"
      );
      const data = await response.json();
      setProgress(data);
    } catch (error) {
      console.error("Failed to fetch progress", error);
    }
  };

  //     
  useEffect(() => {
    fetchProgress();
  }, []);
  //   
  const handleEdit = (index) => {
    if (!user) {
      alert(" .");
      return;
    }
    
    if (user.status !== 'a') {
      alert("  .");
      return;
    }
    setEditIndex(index);
    setEditedData({ ...progress[index] });
  };

  //   
  const handleSave = async (index) => {
    try {
      const item = { ...editedData };

      //    
      if (item.taskStatus === "") {
        item.completionPercentage = 0;
      } else if (item.taskStatus === "") {
        item.completionPercentage = 100;
      }

      const response = await fetch(
        `https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/ProjectProgress/update`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(item),
        }
      );

      if (!response.ok) throw new Error("Failed to update progress");

      const updatedProgress = [...progress];
      updatedProgress[index] = item;
      setProgress(updatedProgress);
      setEditIndex(null);
    } catch (error) {
      console.error("Error updating progress:", error);
    }
  };

  //  
  const handleCancel = () => {
    setEditIndex(null);
    setEditedData({});
  };

  //   
  const handleChange = (e) => {
    const { name, value } = e.target;
    setEditedData((prev) => {
      let newValue = value;

      //   
      if (name === "completionPercentage") {
        newValue = Math.min(Math.max(parseInt(value, 10), 0), 100) || ""; // 0 ~ 100 
      }

      //     
      if (name === "taskStatus") {
        if (value === "") {
          return { ...prev, [name]: value, completionPercentage: 0 };
        } else if (value === "") {
          return { ...prev, [name]: value, completionPercentage: 100 };
        }
      }

      return { ...prev, [name]: newValue };
    });
  };

  //   
  const handleAdd = () => {
    if (!user) {
      alert(" .");
      return;
    }
    
    if (user.status !== 'a') {
      alert("  .");
      return;
    }
    setNewProgress({
      taskName: "",
      taskStatus: "",
      completionPercentage: 0,
    });
  };

  const handleSaveNew = async () => {
    try {
      const response = await fetch(
        "https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/ProjectProgress/add",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(newProgress),
        }
      );

      const result = await response.json();
      if (result.status !== "success") throw new Error(result.message);

      console.log(result.message); //   
      setProgress((prev) => [...prev, newProgress]);
      setNewProgress(null);
    } catch (error) {
      console.error("Error adding new progress:", error);
    }
  };

  //    
  const handleCancelNew = () => {
    setNewProgress(null);
  };

  const handleNewChange = (e) => {
    const { name, value } = e.target;
    setNewProgress((prev) => {
      let newValue = value;

      if (name === "completionPercentage") {
        newValue = Math.min(Math.max(parseInt(value, 10), 0), 100) || ""; // 0 ~ 100 
      }

      return { ...prev, [name]: newValue };
    });
  };

  //    
  const handleRemove = async (index) => {
    if (!user) {
      alert(" .");
      return;
    }
    
    if (user.status !== 'a') {
      alert("  .");
      return;
    }
    const isConfirmed = window.confirm("   ?");
    if (!isConfirmed) return;

    try {
      const taskId = progress[index].taskId;

      const response = await fetch(
        `https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/ProjectProgress/delete/${taskId}`,
        {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (!response.ok) throw new Error(" .");

      const updatedProgress = progress.filter((_, i) => i !== index);
      setProgress(updatedProgress);

      console.log("  .");
    } catch (error) {
      console.error("     :", error);
    }
  };

  const calculateTotalProgress = () => {
    const totalProgress = progress.reduce(
      (total, item) => total + item.completionPercentage,
      0
    );
    const averageProgress = totalProgress / progress.length;
    return averageProgress.toFixed(2); //   
  };

  return (
    <div className="main-page-container">
      <div className="left-container">
        <span className="main-title"> ERP </span>
        <p className="description">
            ERP       
           .
        </p>
        <table className="info-table">
        <tbody>
          <tr>
            <th>  </th>
            <td>Java (17 ), JavaScript (ES6 )</td>
          </tr>
          <tr>
            <th>  </th>
            <td>IntelliJ IDEA, Visual Studio Code</td>
          </tr>
          <tr>
            <th>  </th>
            <td>Spring Boot 3 (REST API )</td>
          </tr>
          <tr>
            <th>  </th>
            <td>React (React Router  Context API )</td>
          </tr>
          <tr>
            <th> DB </th>
            <td>Mysql 8.0.34</td>
          </tr>
          <tr>
            <th>  </th>
            <td>AppPass </td>
          </tr>
          <tr>
            <th>  API</th>
            <td> API (PG     )</td>
          </tr>
        </tbody>
      </table>

      <div className="progress-container">
      <span className="main-title">  &nbsp;
        <small>(  : {calculateTotalProgress()}%)</small>
      </span>
      <button className="add-button" onClick={handleAdd}></button>
    </div>
      <table className="progress-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {progress.length > 0 ? (
            progress.map((item, index) => (
              <tr key={index}>
                <td>{item.taskName}</td>
                <td>
                  {editIndex === index ? (
                    <select
                      name="taskStatus"
                      value={editedData.taskStatus || ""}
                      onChange={handleChange}
                    >
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""></option>
                    </select>
                  ) : (
                    item.taskStatus
                  )}
                </td>
                <td>
                  {editIndex === index ? (
                    <input
                      type="number"
                      name="completionPercentage"
                      value={editedData.completionPercentage || ""}
                      onChange={handleChange}
                      min="0"
                      max="100"
                    />
                  ) : (
                    <div className="progress-bar">
                      <div
                        className="progress"
                        style={{ width: `${item.completionPercentage}%` }}
                      >
                        {item.completionPercentage}%
                      </div>
                    </div>
                  )}
                </td>
                <td>
                  {editIndex === index ? (
                    <>
                      <button onClick={() => handleSave(index)}></button>
                      <button onClick={handleCancel}></button>
                    </>
                  ) : (
                    <>
                      <button className="edit-button" onClick={() => handleEdit(index)}>
                        
                      </button>
                      <button className="remove-button" onClick={() => handleRemove(index)}>
                        
                      </button>
                    </>
                  )}
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="4">  .</td>
            </tr>
          )}
           {/*    */}
           {newProgress && (
            <tr>
              <td>
                <input
                  type="text"
                  name="taskName"
                  value={newProgress.taskName}
                  onChange={handleNewChange}
                  placeholder="  "
                />
              </td>
              <td>
                <select
                  name="taskStatus"
                  value={newProgress.taskStatus}
                  onChange={handleNewChange}
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </td>
              <td>
                <input
                  type="number"
                  name="completionPercentage"
                  value={newProgress.completionPercentage}
                  onChange={handleNewChange}
                  min="0"
                  max="100"
                  placeholder="0 ~ 100"
                />
              </td>
              <td>
                <button onClick={handleSaveNew}></button>
                <button onClick={handleCancelNew}></button>
              </td>
            </tr>
          )}
        </tbody>
      </table>
      </div>
      <div className="right-container">
        <TaskLogTable/>
        </div>
    </div>
  );
};

export default ProjectInfo;
