import React, { useState, useEffect } from "react";
import axios from "axios";
import './TaskLogTable.css'; // CSS  .

const TaskLogTable = () => {
  const [taskLogs, setTaskLogs] = useState([]);
  const [currentPage, setCurrentPage] = useState(1); //   
  const [pageSize, setPageSize] = useState(5); //     
  const [progress, setProgress] = useState([]);
  const user = JSON.parse(localStorage.getItem('user'));
  
  
  const [formData, setFormData] = useState({
    logId: "",
    taskId: "",
    taskName: "",
    title: "",
    description: "",
    taskDate: "",
  });
  const [editing, setEditing] = useState(false);
  const [showForm, setShowForm] = useState(false); //     
  const [error, setError] = useState(""); //    
  

  //  TaskLog  
  const fetchTaskLogs = async () => {
    try {
      const response = await axios.get("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/tasklog/recent");
      setTaskLogs(response.data); //    
    } catch (error) {
      console.error("Failed to fetch task logs:", error);
    }
  };

  useEffect(() => {
    fetchTaskLogs();
  }, []);
  const handleChange = (e) => {
    const { name, value } = e.target;
  
    if (name === "taskName") {
      //  taskName  taskId 
      const selectedTask = progress.find((item) => item.taskName === value);
      setFormData({
        ...formData,
        taskName: value,
        taskId: selectedTask ? selectedTask.taskId : "", // taskId 
      });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };


  //  
  const handleAdd = async (event) => {
    if (!user) {
      alert(" .");
      return;
    }
    
    if (user.status !== 'a') {
      alert("  .");
      return;
    }

    if (validateForm()) {
      try {
        await axios.post("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/tasklog", formData);
        fetchTaskLogs(); //     .
        setFormData({
          logId: "",
          taskId: "",
          taskName: "",
          title: "",
          description: "",
          taskDate: "",
        });
        setShowForm(false); //  
        setError(""); //  
      } catch (error) {
        console.error("Failed to add task log:", error);
      }
    }
  };

  //  
  const handleUpdate = async (event) => {
    if (!user) {
      alert(" .");
      return;
    }
    
    if (user.status !== 'a') {
      alert("  .");
      return;
    }
    if (validateForm()) {
      try {
        await axios.put(`https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/tasklog/${formData.logId}`, formData);
        fetchTaskLogs(); //     .
        setEditing(false);
        setShowForm(false); //  
        setFormData({
          logId: "",
          taskId: "",
          taskName: "",
          title: "",
          description: "",
          taskDate: "",
          uptDate: ""
        });
        setError(""); //  
      } catch (error) {
        console.error("Failed to update task log:", error);
      }
    }
  };

  //   
  const validateForm = () => {
    if (!formData.taskName || !formData.title || !formData.description) {
      setError("  .");
      return false;
    }
    return true;
  };

  //  
  const handleDelete = async (logId) => {
    if (!user) {
      alert(" .");
      return;
    }
    
    if (user.status !== 'a') {
      alert("  .");
      return;
    }
    try {
      await axios.delete(`https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/tasklog/${logId}`);
      fetchTaskLogs(); //     .
      setShowForm(false);
    } catch (error) {
      console.error("Failed to delete task log:", error);
    }
  };

  //   
  const handleEdit = (log) => {
    setFormData(log);
    setEditing(true);
    setShowForm(true); //  
    setError(""); //  
  };

  //   "  "    
  const handleAddButtonClick = () => {
    setShowForm(true); //  
    setEditing(false);
    setFormData({
      logId: "",
      taskId: "",
      taskName: "",
      title: "",
      description: "",
      taskDate: "",
    });
    setError(""); //  
  };

  //   
  const paginatedTaskLogs = taskLogs.slice((currentPage - 1) * pageSize, currentPage * pageSize);

  //   
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const fetchProgress = async () => {
    try {
      const response = await axios.get("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/ProjectProgress/all");
      setProgress(response.data);
    } catch (error) {
      console.error("Failed to fetch progress", error);
    }
  };

  useEffect(() => {
    fetchProgress();
  }, []);

  return (
    <div className="task-log-container">
      <div className="same-line">
        <span className="main-title"> </span>
        {/*     */}
        <button className="add-button" onClick={handleAddButtonClick}>  </button>
      </div>
      <table className="log-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {paginatedTaskLogs.length > 0 ? paginatedTaskLogs.map((log) => (
            <tr key={log.logId} onClick={() => handleEdit(log)} style={{ cursor: 'pointer' }}>
              <td>{log.logId}</td>
              <td className="log-taskName">{log.taskName}</td>
              <td className=" log-title">
                {log.title}
              </td>
              <td>{new Date(log.taskDate).toLocaleDateString()}</td>
              <td>{log.uptDate ? new Date(log.uptDate).toLocaleDateString() : ""}</td> {/*   */}
            </tr>
          )) : <tr><td colSpan="5">  .</td></tr>}
        </tbody>
      </table>

      {/*   */}
      <div className="pagination">
        {Array.from({ length: Math.ceil(taskLogs.length / pageSize) }, (_, index) => (
          <button key={index + 1} onClick={() => handlePageChange(index + 1)}>
            {index + 1}
          </button>
        ))}
      </div>

      {/* /  */}
      {showForm && (
        <div className="Add-form-container">
          <div className="form-header">
                <h2 className="Add-form-title"> </h2>
                <button
                  className="close-button"
                  onClick={() => setShowForm(false)} // X     
                >
                  &times;
                </button>
              </div>
            {error && <p className="error">{error}</p>}
            <form
            onSubmit={(e) => {
                e.preventDefault();
                editing ? handleUpdate() : handleAdd();
            }}
            >
            <ul>
            <li>
              <label>  : </label>
              <select
                name="taskName"
                value={formData.taskName}
                onChange={(e) => handleChange(e)}
              >
                <option value="">  </option>
                {progress.map((item) => (
                  <option key={item.taskId} value={item.taskName}>
                    {item.taskName}
                  </option>
                ))}
              </select>
            </li>

                <li>
                <label> : </label>
                <input
                    type="text"
                    name="title"
                    value={formData.title}
                    onChange={handleChange}
                    required
                    placeholder="  ."
                />
                </li>
                <li>
                  <label> : </label>
                  <textarea
                      name="description"
                      value={formData.description}
                      onChange={handleChange}
                      required
                      placeholder="   ."
                  />
                </li>
                <li>
                <label>  : </label>
                  {editing ? (
                    <input
                      type="text"
                      name="taskDate"
                      value={new Date(formData.taskDate).toLocaleDateString()}
                      onChange={handleChange}
                      required
                      disabled={editing} //   
                    />
                  ) : (
                    <input
                      type="text"
                      name="taskDate"
                      value={new Date().toLocaleDateString()} //    
                      readOnly //   
                    />
                  )}
                  </li>
                  {formData.uptDate && 
                    <li>
                      <label>  : </label>
                      <input type="text"
                      name="uptDate"
                      value={new Date(formData.uptDate).toLocaleDateString()}
                      disabled={editing} //   
                      />
                    </li>}
            </ul>
            <div className="form-actions">
                <button className="action-button" type="submit">{editing ? "" : ""}</button>
                {editing && (
                <button
                    type="button"
                    className="action-button"
                    onClick={() => {
                    if (window.confirm(" ?")) {
                        handleDelete(formData.logId);
                    }
                    }}
                >
                    
                </button>
                )}
            </div>
            </form>
        </div>
        )}


    </div>
  );
};

export default TaskLogTable;
