import React, { useState, useEffect } from "react";
import "./AddSales.css";

const AddSales = ({ onClose, onSave }) => {
  const [formData, setFormData] = useState({
    userNum: "", // userNum 
    itemNum: "",
    salesQuantity: "",
    totalPrice: "",
    totalCostPrice: "",
    salesRegDate: "",
    payType: "",
  });
  const [itemList, setItemList] = useState([]); //  
  const [selectedItem, setSelectedItem] = useState(null); //   

  useEffect(() => {
    //    
    const fetchItemList = async () => {
      try {
        const response = await fetch("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/payment/getItemNumQuantityList");
        if (!response.ok) {
          throw new Error("   .");
        }
        const data = await response.json();
        setItemList(data.items); // items : { itemNum, itemName, itemQuantity, ... }
      } catch (error) {
        console.error(error.message);
      }
    };

    fetchItemList();
  }, []);

  const handleItemChange = (e) => {
    const selectedNum = e.target.value;
    const item = itemList.find((i) => i.itemNum === selectedNum) || null;
    setSelectedItem(item); //    
    setFormData((prev) => ({ ...prev, itemNum: selectedNum, salesQuantity: "", totalPrice: "", totalCostPrice: "" }));
  };

  const handleQuantityChange = (e) => {
    const quantity = e.target.value;
    if (selectedItem && quantity > selectedItem.itemQuantity) {
      alert(`  ${selectedItem.itemQuantity}  .`);
      return;
    }
    const totalPrice = quantity * selectedItem?.price || 0;
    const totalCostPrice = quantity * selectedItem?.costPrice || 0;
    setFormData((prev) => ({
      ...prev,
      salesQuantity: quantity,
      totalPrice: totalPrice,
      totalCostPrice: totalCostPrice,
    }));
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (
      !formData.userNum ||
      !formData.itemNum ||
      !formData.salesQuantity ||
      !formData.totalPrice ||
      !formData.salesRegDate ||
      !formData.payType
    ) {
      alert("  .");
      return;
    }
  
    //   
    try {
      const response = await fetch("https://n0b85a7897a3e9c3213c819af9d418042.apppaas.app/payment/selfInsert", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });
  
      if (response.ok) {
        const result = await response.json();
        alert("  ");
      onClose(); //  
      window.location.reload(); //  
      } else {
        alert("  .");
      }
    } catch (error) {
      console.error("  :", error);
      alert("  .");
    }
}
  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2 className="modal-title">  </h2>
        <form className="modal-form" onSubmit={handleSubmit}>
          <div className="modal-form-group">
            <label className="modal-label">  :</label>
            <input
              type="text"
              name="userNum"
              className="modal-input"
              value={formData.userNum}
              onChange={handleChange}
              required //    
            />
          </div>

          <div className="modal-form-group">
            <label className="modal-label">  :</label>
            <select
              name="itemNum"
              className="modal-select"
              value={formData.itemNum}
              onChange={handleItemChange}
            >
              <option value=""></option>
              {itemList.map((item) => (
                <option key={item.itemNum} value={item.itemNum}>
                  {item.itemNum}/{item.itemName}
                </option>
              ))}
            </select>
          </div>

          <div className="modal-form-group">
            <label className="modal-label">  :</label>
            <input
              type="number"
              name="salesQuantity"
              className="modal-input"
              value={formData.salesQuantity}
              onChange={handleQuantityChange}
              disabled={!selectedItem} //     
            />
          </div>
          {selectedItem && (
            <div className="inventory-info">
              (  : {selectedItem.itemQuantity})
            </div>
          )}

          <div className="modal-form-group">
            <label className="modal-label">  :</label>
            <input
              type="number"
              name="totalPrice"
              className="modal-input"
              value={formData.totalPrice}
              readOnly
            />
          </div>

          <div className="modal-form-group">
            <label className="modal-label"> :</label>
            <input
              type="number"
              name="totalCostPrice"
              className="modal-input"
              value={formData.totalCostPrice}
              readOnly
            />
          </div>

          <div className="modal-form-group">
            <label className="modal-label">  :</label>
            <input
              type="date"
              name="salesRegDate"
              className="modal-input"
              value={formData.salesRegDate}
              onChange={handleChange}
            />
          </div>

          <div className="modal-form-group">
            <label className="modal-label">  :</label>
            <select
              name="payType"
              className="modal-select"
              value={formData.payType}
              onChange={handleChange}
            >
              <option value=""></option>
              <option value="Card"></option>
              <option value="Cash"></option>
              <option value="Credit"> </option>
              <option value="BankTransfer"> </option>
              <option value="GiftCard"></option>
              <option value="MobilePayment"> </option>
              <option value="Check"></option>
              <option value="Other"></option>
            </select>
          </div>

          <div className="modal-actions">
            <button type="submit" className="modal-button modal-button-save">
              
            </button>
            <button
              type="button"
              className="modal-button modal-button-cancel"
              onClick={onClose}
            >
              
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddSales;
